<template>
  <div class="content-index">
    <div class="content-header">
      <search tips="搜索桌号" @emitSearchFun="searchFun($event)" @emitClearFun="clearFun()" />
      <router-link class="content-salesPoints" to="/mobileOrdering/salesPoints">
        <div class="iconfont icon-didian"></div>
        <div class="salesPoints-text">{{salesPoint.code == -1?"营业点":salesPoint.descript}}</div>
      </router-link>
    </div>
    <div class="content-nav">
      <div
        class="item"
        :class="activeItem==index?'activeItem':''"
        v-for="(item,index) in navList"
        :key="index"
        @click="switchFun(index,item)"
      >
        <div class="nav-icon" v-if="item.color" :style="{backgroundColor:item.color}"></div>
        <div class="nav-text">{{item.text}}</div>
      </div>
    </div>
    <div class="content-main">
      <mt-loadmore :top-method="loadTop" :bottom-all-loaded="allLoaded" ref="loadmore">
        <div>
          <div class="list" v-if="mealList.length>0">
            <div class="item" v-for="(item, index) in mealList" :key="index" @click="gotoFun(item)">
              <div class="item-head" :style="{backgroundColor:item.color}">
                <template v-if="item.sta==0">空闲</template>
                <template v-else-if="item.sta==1">预定</template>
                <template v-else-if="item.sta==2">使用</template>
                <template v-else>临时</template>
              </div>
              <!-- <div class="item-head">临时</div> -->
              <div class="item-num">{{item.code}}</div>
              <div class="item-name">{{item.tableDescript}}</div>
              <div
                class="iconfont icon-chifan icon-chifan-already"
                v-if="item.sta==2 && item.base==2"
              ></div>
              <div
                class="iconfont icon-chifan icon-chifan-no"
                v-else-if="item.sta==2 && item.base==1"
              ></div>
            </div>

            <div
              class="item"
              v-for="(item, index) in supplementList"
              :key="'s'+index"
              style="opacity: 0;"
            ></div>
          </div>
          <div class="no-list" v-else>
            <noResult colors="#fff"></noResult>
          </div>
        </div>
      </mt-loadmore>
    </div>
    <mainFooter activeItem="0"></mainFooter>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast, Loadmore } from "mint-ui";
import search from "@/components/common/mobileOrdering/search.vue";
import mainFooter from "@/components/common/mobileOrdering/mainFooter.vue";
import noResult from "@/components/common/mobileOrdering/noResult.vue";

import { mapState, mapGetters } from "vuex";
export default {
  components: {
    search,
    mainFooter,
    noResult
  },
  data() {
    return {
      navList: [
        { text: "全部", color: "" },
        { text: "空闲", color: "#00D69B" },
        { text: "使用", color: "#FF9028" },
        { text: "预订", color: "#2C8EFF" },
        { text: "微信", color: "#009D00" },
        { text: "临时", color: "#FF5960" }
      ],
      mealListAll: [],
      mealListFiter: [],
      mealList: [],
      supplementList: [],
      activeItem: 0,
      list: [],

      allLoaded: false
    };
  },
  computed: {
    ...mapState({
      salesPoint: state => state.mobileOrdering.salesPoint,
      tableType: state => state.mobileOrdering.tableType,
      tempTable: state => state.mobileOrdering.tempTable
    })
  },
  created() {
    this.$store.commit("clearSelectedTableInfo");
    this.$store.commit("clearSelectedPreOrder");
    this.$store.commit("clearEntryPreOrderUrl");
    //this.$store.commit("clearTempTable");
  },
  mounted() {
    this.getData();
  },
  methods: {
    loadTop() {
      this.getData();
      this.$refs.loadmore.onTopLoaded();
    },

    async getData() {
      let data = {
        params: {}
      };
      let res = await ajaxGet("/pos/router?method=getTableMaps", data);
      if (res && res.resultCode == 0) {
        this.mealListAll = res.data;
        this.mealListAll.forEach(opt => {
          if (opt.isWechat == 1) {
            this.$set(opt, "color", "#009D00");
          } else {
            if (opt.sta == 0) {
              this.$set(opt, "color", "#00D69B");
            } else if (opt.sta == 1) {
              this.$set(opt, "color", "#2C8EFF");
            } else if (opt.sta == 2) {
              this.$set(opt, "color", "#FF9028");
            }
          }
        });
        this.mealList = this.mealListFiter = this.mealListAll.filter(ele => {
          if (
            (this.salesPoint.code == -1 ||
              ele.pccode == this.salesPoint.code) &&
            (this.tableType.code == -1 || ele.type == this.tableType.code)
          ) {
            return ele;
          }
        });
        this.switchFun(this.activeItem, "");
      }
    },
    searchFun(val) {
      //  console.log("val", val);
      this.activeItem = -1;
      this.mealList = this.mealListAll.filter(ele => {
        if (ele.code.indexOf(val) > -1) {
          // console.log(ele);
          return ele;
        }
      });
      this.filling();
    },
    clearFun() {
      this.activeItem = 0;
      this.mealList = this.mealListFiter;

      this.filling();
    },
    gotoFun(item) {
      //console.log("item:", item);
      if (item.sta == 0) {
        this.$store.commit("sumbitSelectedTableInfo", item);
        this.$router.push("/mobileOrdering/openTable");
      } else if (item.sta == 1) {
        this.$store.commit("submitSelectedPreOrder", item);
        this.$store.commit("submiEntryPreOrderUrl", "/mobileOrdering/index");
        this.$router.push("/mobileOrdering/preOrderDetails");
      } else if (item.sta == 2) {
        this.$store.commit("sumbitSelectedTableInfo", item);
        this.$router.push("/mobileOrdering/myOrder");
      }
    },
    switchFun(index, item) {
      this.activeItem = index;
      if (index == 5) {
        this.mealList = [this.tempTable];
      } else {
        this.mealList = this.mealListFiter.filter(ele => {
          if (index == 0) {
            return ele;
          } else if (index == 1 && ele.sta == 0) {
            return ele;
          } else if (index == 2 && ele.sta == 2) {
            return ele;
          } else if (index == 3 && ele.sta == 1) {
            return ele;
          } else if (index == 4 && ele.isWechat == 1) {
            return ele;
          }
        });
      }
      this.filling();
    },
    filling() {
      this.supplementList = [];
      for (let i = 0; i < 4 - (this.mealList.length % 4); i++) {
        this.supplementList.push("");
      }
    },

    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less">
.content-index {
  height: 100vh;
  background-color: #393c41;
  overflow: hidden;
  .content-header {
    background-color: #393c41;
    height: 45px;
    width: 100%;
    display: flex;
    align-items: center;
    padding-top: 7px;
    padding-left: 15px;
    padding-right: 12px;
    box-sizing: border-box;
    .content-salesPoints {
      display: flex;
      align-items: center;
      margin-left: 15px;
      .icon-didian {
        color: #fff;
      }
      .salesPoints-text {
        color: #fff;
        text-align: left;
      }
    }
  }
  .content-nav {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #ffffff;
    background-color: #393c41;

    .item {
      width: 20%;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      border-bottom: 1px solid #393c41;
      .nav-icon {
        width: 14px;
        height: 12px;
        border-radius: 2.52px;
        margin-right: 5px;
      }
      .nav-text {
        text-align: center;
      }
    }
    .activeItem {
      border-bottom-color: #fff;
    }
  }
  .content-main {
    position: absolute;
    top: 100px;
    bottom: 50px;
    overflow-y: auto;
    width: 100%;
    background-color: #343b51;
    .mint-loadmore {
      min-height: 100%;
    }
    .list {
      overflow-y: auto;
      padding: 10px 15px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      align-content: baseline;
      width: 100%;
      box-sizing: border-box;
      .item {
        margin-bottom: 10px;
        border-radius: 5px;
        overflow: hidden;
        width: 78px;
        height: 83px;
        position: relative;
        text-align: center;
        background-color: #fff;
        .item-head {
          font-size: 12px;
          color: #ffffff;
          height: 20px;
          line-height: 20px;
        }
        .item-num {
          margin-top: 5px;
          font-size: 14px;
          color: #000000;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .item-name {
          font-size: 12px;
          color: #000000;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .icon-chifan {
          position: absolute;
          right: 7px;
          bottom: 3px;

          font-size: 12px;
        }
        .icon-chifan-already {
          color: #43d29f;
        }
        .icon-chifan-no {
          color: #fe6766;
        }
      }
      .item:nth-child(4n) {
        margin-right: 0;
      }
    }
    .no-list {
      margin-top: 150px;
    }
  }
}
</style>



